








































































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import {Loading, QSpinnerGears, QRouteTab, colors} from 'quasar'


@Component
export default class app extends Vue {

/* -------------------------------------------------------------------------------------------------------------
                                        SPINNER
------------------------------------------------------------------------------------------------------------- */ 
get loading() {
    // customizable (all props available)
      window.console.log('(this.$store.state.general.loading: ' + this.$store.state.general.loading)
      if (this.$store.state.general.loading === true){
        Loading.show({
          //spinner: QSpinnerGears,
          message:  `<h4>Du willst ein hochauflösendes Poster auf hochwertigem Papier? Genau das 
                      bereiten wir im Hintergrund gerade für dich vor. Du hast gleich die Möglichkeit,
                      dein Werk zu bewundern. Einen Moment bitte.</h4>` ,

          messageColor: 'black',
          spinnerSize: 200, // in pixels
          spinnerColor: 'black',
          backgroundColor: 'white'
          
      })
      } else if (this.$store.state.general.loading === false){
            Loading.hide()
      }
    return this.$store.state.general.loading;
    }

  @Watch('loading')
   OnChange(value: boolean, oldvalue: boolean){
   window.console.log('loading' +  this.$store.state.general.loading)      
   return this.$store.state.general.loading;
 }


 mounted(){
   window.console.log("BASE URL: " +this.$store.state.general.base_url)
 }

}
